import { Component, OnInit } from '@angular/core';
import {RoutingState} from '../services/RoutingHistory/routingState.service';
import {HomeService} from '../services/HomeService/home.service';

@Component({
  selector: 'app-my-shows',
  templateUrl: './my-shows.component.html',
  styleUrls: ['./my-shows.component.scss']
})
export class MyShowsComponent implements OnInit {

  user:string;
  shows = [];

  constructor(routingState: RoutingState,private homeService: HomeService) {
    this.user = routingState.getUser();
  }

  ngOnInit() {
    let usersFilms=[];

    this.homeService.getUsersShows({user: this.user}).subscribe(show => {
      usersFilms = show.body;
      console.log('USERS_SHOWS',usersFilms);
      usersFilms.map(item => {
        const s = this;
        console.log(s);
        this.homeService.getCertainShow(item).subscribe(sho => {
          console.log(s.shows);
          s.shows.push(sho.body);
          console.log('MYYYYYYYYYYYYYY',this.shows);
        });
      });
    });
  }

}
