import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators} from '@angular/forms';
import {Auth} from '../services/AuthService/auth.service';
import { Router } from '@angular/router';
import {RoutingState} from '../services/RoutingHistory/routingState.service';
import { AuthService, GoogleLoginProvider } from 'angularx-social-login';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['../app.component.scss',"../../../node_modules/bootstrap/dist/css/bootstrap.min.css"],
  styles: [`input.ng-touched.ng-invalid {border:solid red 2px;}
  input.ng-touched.ng-valid {border:solid green 2px;}`]
})
export class SignupComponent implements OnInit {

  signupForm : FormGroup;
  passwordText='';
  emailText='';
  confirmPassword='';
  previousRoute: string;

  constructor(private authService: Auth, private router: Router,private routingState: RoutingState, private _socioAuthServ: AuthService) {
    this.signupForm = new FormGroup({

      passwordText: new FormControl("signupForm", Validators.compose([
        Validators.required,
        Validators.minLength(6),
        Validators.maxLength(30),
        this.PasswordValidator
      ])),
      confirmPassword: new FormControl("signupForm", Validators.compose([
        Validators.required,
        Validators.minLength(8),
        Validators.maxLength(30),
        this.PasswordValidator
      ])),
      emailText: new FormControl("signupForm", [Validators.required, Validators.email]),
  });
  }

  private PasswordValidator(control: FormControl): {[s:string]:boolean} {

    if(/^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/.test(control.value)){
      return {"signupForm": true};
    }
    return null;
  }

  singInGoogle(platform : string): void {
    platform = GoogleLoginProvider.PROVIDER_ID;
    console.log('PLATFORM', platform);
    this._socioAuthServ.signIn(platform).then(
      (response) => {
        console.log('RESPONSE',response);
        console.log(platform + " logged in user data is= " , response);
        this.authService.Signup({email:response.email, password:''})
        this.authService.setUserEmail(response.email);
        this.router.navigate([`${this.previousRoute}`]);
      }, reject => {
        console.log(reject);
      }
    )
  }

  signup(){
    console.log('REGISTER');
    if(this.confirmPassword !== this.passwordText) {
      this.passwordText='';
      this.confirmPassword='';
      return;
    }
    this.authService.Signup({
      email:this.emailText,
      password:this.passwordText
    }).subscribe(show=>{
      console.log(show);
      this.authService.setUserEmail(this.emailText);
      this.router.navigate([`${this.previousRoute}`]);
    });
  }

    // Method to sign in with google.

  userNameValidator(control: FormControl): {[s:string]:boolean}{

    if(control.value==="нет"){
        return {"showName": true};
    }
    return null;
  }

  // signup(){
  //   this.homeService.postShows(this.showName).subscribe(show=>{
  //     console.log(show);
  //   });
  //   console.log(this.myForm);
  // }

  ngOnInit() {
    // this.authService.email.subscribe((email:string)=>{
    //   this.emailText = email;
    // })
    this.previousRoute = this.routingState.getPreviousUrl();
  }

}
